.bread-crumbs {
	&__content {
		padding: 15px 0;

		ul {
			display: flex;
			align-items: center;
			column-gap: 5px;

			li {
				a {
					font-size: 17px;
					line-height: 56px;
					@include aFont(17, 14, 1);
					@include avPx('line-height', 56, 20, 1);
					font-weight: 500;
					color: #7D7D7D;
					text-decoration-line: underline;
					text-underline-offset: 3px;
					text-decoration-color: transparent;
					transition: text-decoration-color .4s ease 0s;

					@media (any-hover: hover) {
						&:hover {
							text-decoration-color: inherit;
						}
					}
				}

				i {
					font-size: 17px;
					line-height: 56px;
					@include aFont(17, 14, 1);
					@include avPx('line-height', 56, 20, 1);
					font-weight: 500;
					color: #7D7D7D;
				}

				span {
					font-size: 17px;
					line-height: 56px;
					@include aFont(17, 14, 1);
					@include avPx('line-height', 56, 20, 1);
					font-weight: 500;
					color: #7D7D7D;
				}
			}
		}
	}
}
