* {
	outline: transparent;
	padding: 0;
	margin: 0;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

html,
body {
	height: 100%;
}

button, input, textarea {
	font-family: inherit;
	font-size: inherit;
	border: none;
	background: inherit;
}

button {
	cursor: pointer;
}

a {
	text-decoration: none;
	color: inherit;
}

ul, ol {
	list-style: none;
}
