.description {

	&__content {
		padding-bottom: 47px;

		@media (max-width: 768px) {
			padding-bottom: 40px;
		}
	}

	&__header {
		margin-bottom: 15px;
	}

	&__title {
		font-size: 30px;
		line-height: 56px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 56, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__body {
		max-width: 1082px;
		display: flex;
		flex-direction: column;
		row-gap: 56px;

		@media (max-width: 1366px) {
			row-gap: 15px;
		}

		p {
			font-size: 17px;
			line-height: 35px;
			@include aFont(17, 12, 1);
			@include avPx('line-height', 35, 20, 1);
			color: #000;
		}
	}
}
