.wrapper.educational {
	.information {

		&__content {
			padding-bottom: 134px;

			@media (max-width: 768px) {
				padding-bottom: 50px;
			}
		}

		&__header {
			margin-bottom: 15px;
		}

		&__body {
			margin-bottom: 0;

			strong {
				@media (max-width: 768px) {
					line-height: 20px;
				}
			}
		}

		&__items {
			display: grid;
			grid-template-columns: repeat(2, auto);
			gap: 36px 60px;
			margin-bottom: 0;

			@media (max-width: 768px) {
				gap: 20px;
			}

			@media (max-width: 520px) {
				grid-template-columns: auto;
			}
		}

		&__item {

			@media (max-width: 768px) {
				align-items: flex-start;
			}
		}

		&__item-icon {
			svg {
				display: block;
				max-width: 100%;
			}

			@media (max-width: 520px) {
				width: 40px;
			}
		}
	}
}
