.footer {
	background: #101923;

	&__content {
		display: flex;
		flex-direction: column;
		row-gap: 22px;
		padding: 35px 0 32px;

		* {
			color: #FFFFFF;
		}
	}

	&__logo {
		a {
			img {
				display: block;
				max-width: 100%;
			}
		}
	}

	&__inner {
		width: 100%;
		display: flex;
		flex-direction: column;
		row-gap: 22px;
	}

	&__body {
		display: flex;
		align-items: center;
		column-gap: 51px;

		@media (max-width: 992px) {
			column-gap: 20px;
		}

		@media (max-width: 767px) {
			flex-direction: column;
			row-gap: 20px;
		}
	}

	&__items {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: 10px 20px;

		@media (max-width: 767px) {
			justify-content: center;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		column-gap: 14px;

		a {
			display: flex;
			align-items: center;
			column-gap: 14px;
			font-size: 12px;
			font-weight: 600;
			color: #FFF;

			address {
				font-style: normal;
				transition: color .4s ease 0s;
			}

			span {
				transition: color .4s ease 0s;
			}

			@media (any-hover: hover) {
				&:hover {
					address, span {
						color: #006EDE;
					}
				}
			}
		}

		p, span {
			font-size: 12px;
			font-weight: 600;
			color: #FFF;
		}

		&_vk {
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 34px;
				height: 34px;
				border-radius: 6px;
				background: #006EDE;
				transition: background .4s ease 0s;

				@media (any-hover: hover) {
					&:hover {
						background: transparent;
					}
				}

			}
		}

		&_phone {
			column-gap: 5px;

			a {
				column-gap: 8px;
				font-weight: 700;
			}
		}
	}

	&__menu {
		nav {
			ul {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				gap: 26px;

				@media (max-width: 1070px) {
					justify-content: center;
				}

				li {
					a {
						font-size: 12px;
						font-weight: 700;
						color: #FFF;
						transition: color .4s ease 0s;

						@media (any-hover: hover) {
							&:hover {
								color: #006EDE;
							}
						}
					}
				}
			}
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		column-gap: 5px;

		a, p {
			font-size: 12px;
			font-weight: 500;
			line-height: 30px;
			color: #7D7D7D;
		}

		a {
			text-decoration-line: underline;
			text-underline-offset: 3px;
			text-decoration-color: transparent;
			transition: color .4s ease 0s, text-decoration-color .4s ease 0s;

			@media (any-hover: hover) {
				&:hover {
					text-decoration-color: #006EDE;
					color: #006EDE;
				}
			}
		}
	}
}
