.information {

	&__header {
		margin-bottom: 31px;

		@media (max-width: 768px) {
			margin-bottom: 25px;
		}
	}

	&__title {
		font-size: 30px;
		line-height: 56px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 56, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__body {
		margin-bottom: 77px;

		@media (max-width: 768px) {
			margin-bottom: 50px;
		}

		& > a {
			width: max-content;
		}
	}

	&__items {
		display: flex;
		flex-direction: column;
		row-gap: 42px;
		margin-bottom: 40px;

		@media (max-width: 768px) {
			margin-bottom: 25px;
		}

		@media (max-width: 767px) {
			row-gap: 20px;
		}
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		column-gap: 41px;

		@media (max-width: 768px) {
			column-gap: 20px;
		}

		@media (max-width: 767px) {
			align-items: flex-start;
			column-gap: 10px;
		}
	}

	&__item-icon {
		@media (max-width: 520px) {
			position: absolute;
			top: -40px;
			right: 0;
			width: fit-content;
			opacity: .4;
			z-index: 0;
		}

		svg {
			@media (max-width: 767px) {
				display: block;
				max-width: 100%;
			}
		}
	}

	&__item-body {
		position: relative;
		z-index: 1;

		& > strong {
			font-size: 18px;
			line-height: 56px;
			@include aFont(18, 16, 1);
			@include avPx('line-height', 56, 20, 1);
			font-weight: 700;
			color: #000;

			@media (max-width: 520px) {
				display: inline-block;
				margin-bottom: 15px;
			}
		}

		& > P {
			font-size: 15px;
			line-height: 26px;
			@include aFont(15, 12, 1);
			@include avPx('line-height', 26, 20, 1);
			color: #000;
			margin-bottom: 8px;
			opacity: .5;

			@media (max-width: 767px) {
				br {
					display: none;
				}
			}
		}

		& > small {
			font-size: 12px;
			line-height: 26px;
			@include aFont(12, 10, 1);
			@include avPx('line-height', 26, 20, 1);
			color: #000;
			opacity: .5;
		}

		& > a {
			display: flex;
			align-items: center;
			column-gap: 9px;
			font-size: 13px;
			line-height: 29px;
			@include aFont(13, 10, 1);
			@include avPx('line-height', 29, 20, 1);
			color: #000;

			span {
				text-decoration-line: underline;
				text-underline-offset: 3px;
				text-decoration-color: transparent;
				transition: text-decoration-color .4s ease 0s;

				@media (any-hover: hover) {
					&:hover {
						text-decoration-color: #000;
					}
				}
			}
		}
	}
}
