.intro {
	background: linear-gradient(90deg, rgba(32, 32, 32, 0.50) 0.94%, rgba(32, 32, 32, 0.00) 66.35%),
	url(../images/intro/bg.png) no-repeat center/cover;

	&__content {
		position: relative;
		padding: 148px 0 124px;

		@media (max-width: 768px) {
			padding: 50px 0;
		}

		@media (max-width: 520px) {
			padding-bottom: 80px;
		}
	}

	&__slider {
	}
}

.swiper {
}

.swiper-wrapper {
}

.swiper-slide {
	& > h2 {
		font-size: 55px;
		line-height: 60px;
		@include aFont(55, 22, 1);
		@include avPx('line-height', 60, 20, 1);
		font-weight: 700;
		text-transform: uppercase;
		color: #FFF;
		margin-bottom: 35px;
	}

	& > p {
		font-size: 20px;
		line-height: 29px;
		@include aFont(20, 15, 1);
		@include avPx('line-height', 29, 20, 1);
		color: #FFF;
		margin-bottom: 30px;

		@media (max-width: 768px) {
			margin-bottom: 15px;
		}

		@media (max-width: 520px) {
			br {
				display: none;
			}
		}

		&:nth-of-type(2) {
			margin-bottom: 52px;
		}
	}

	& > a {
		width: max-content;
		font-size: 18px;
		@include aFont(18, 16, 1);
		padding: 20px 65px;
	}
}

.btn {
}

.swiper-pagination {
	width: max-content !important;
	left: auto !important;
	right: 0;
	bottom: 25px !important;

	@media (max-width: 520px) {
		left: 0 !important;
		right: 0 !important;
		margin-inline: auto;
	}
}

.swiper-pagination-bullet {
	background: #D9D9D9;
	opacity: 1;

	&.swiper-pagination-bullet-active {
		background: #006EDE;
	}
}
