.company {

	&__content {
		padding-bottom: 67px;

		@media (max-width: 768px) {
			padding-bottom: 50px;
		}
	}

	&__body {
		display: flex;
		margin-bottom: 35px;
		gap: 42px;

		@media (max-width: 1024px) {
			gap: 20px;
			margin-bottom: 25px;
		}

		@media (max-width: 767px) {
			flex-direction: column;
		}
	}

	&__left {
		& > span {
			display: inline-block;
			font-size: 13px;
			@include aFont(13, 10, 1);
			margin-bottom: 10px;
			color: #7D7D7D;
		}

		& > h2 {
			font-size: 35px;
			line-height: 55px;
			@include aFont(35, 20, 1);
			@include avPx('line-height', 55, 20, 1);
			font-weight: 700;
			color: #333;
			margin-bottom: 10px;
		}

		& > p {
			font-size: 16px;
			line-height: 27px;
			@include aFont(16, 12, 1);
			@include avPx('line-height', 27, 20, 1);
			color: #333;
		}

		@media (max-width: 768px) {
			flex: 1;
			br {
				display: none;
			}
		}

		@media (max-width: 767px) {
			flex: unset;
		}
	}

	&__right {
		padding-top: 45px;

		img {
			@media (max-width: 768px) {
				display: block;
				max-width: 100%;
			}
		}

		@media (max-width: 768px) {
			flex: 1;
		}

		@media (max-width: 767px) {
			flex: unset;
			padding-top: 0;
		}
	}

	&__inner {
		h3 {
			font-size: 20px;
			line-height: 27px;
			@include aFont(20, 16, 1);
			@include avPx('line-height', 27, 20, 1);
			font-weight: 700;
			color: #000;
			margin-bottom: 30px;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(3, auto);
		gap: 30px 55px;

		@media (max-width: 1024px) {
			gap: 20px;
		}

		@media (max-width: 768px) {
			grid-template-columns: repeat(2, auto);
		}

		@media (max-width: 767px) {
			grid-template-columns: auto;
		}

		li {
			display: flex;
			column-gap: 20px;

			@media (max-width: 768px) {
				column-gap: 10px;
			}

			.company__icon {
				width: 60px;
				height: 60px;
				display: flex;
				flex-direction: column;
				align-items: flex-start;

				svg {
					display: block;
					max-width: 100%;
				}

				@media (max-width: 768px) {
					width: 30px;
					height: 30px;
				}
			}

			& > p {
				font-size: 15px;
				line-height: 23px;
				@include aFont(15, 12, 1);
				@include avPx('line-height', 23, 20, 1);
				color: #333;
			}
		}
	}
}
