.our-services {

	&__content {
		padding: 60px 0 83px;

		@media (max-width: 768px) {
			padding: 50px 0;
		}
	}

	&__header {
		margin-bottom: 45px;

		@media (max-width: 768px) {
			margin-bottom: 25px;
		}
	}

	&__title {
		font-size: 30px;
		line-height: 32px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 32, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__body {
		ul {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 30px;

			@media (max-width: 1024px) {
				gap: 10px;
			}

			@media (max-width: 768px) {
				display: grid;
				grid-template-columns: repeat(2, auto);
				gap: 5px;
			}

			@media (max-width: 419px) {
				grid-template-columns: auto;
			}

			li {
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				flex: 1 1 30%;
				height: 353px;
				border-radius: 10px;
				background: linear-gradient(202deg, rgba(0, 0, 0, 0.00) 17.84%, #363737 103.81%);
				padding: 44px 22px 56px;
				transition: .4s ease 0s;

				@media (max-width: 1024px) {
					height: 320px;
				}

				@media (max-width: 768px) {
					height: 280px;
					flex: unset;
					padding: 20px;
				}

				@media (max-width: 420px) {
					height: 230px;
				}

				@media (any-hover: hover) {
					&:hover {
						transform: translateY(-5px);

						p, small {
							transform: translateY(-25px);
						}

						a {
							opacity: 1;
							visibility: visible;
							user-select: all;
						}

					}
				}

				img {
					position: absolute;
					inset: 0;
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: inherit;
					margin: auto;
				}

				a {
					position: absolute;
					left: 0;
					bottom: 0;
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					column-gap: 15px;
					border-radius: 0 0 10px 10px;
					background: #006EDE;
					padding: 21px 5px;
					transition: .4s ease 0s;
					opacity: 0;
					visibility: hidden;
					user-select: none;

					span {
						font-size: 20px;
						line-height: 21px;
						font-style: normal;
						font-weight: 400;
						color: #FFF;
					}

					img {
						position: absolute;
						opacity: 0;
					}
				}

				p {
					position: relative;
					font-size: 20px;
					line-height: 21px;
					@include aFont(20, 12, 1);
					@include avPx('line-height', 21, 15, 1);
					font-weight: 700;
					color: #FFF;
					transition: .4s ease 0s;
					z-index: 1;
				}

				small {
					position: relative;
					font-size: 14px;
					line-height: 17px;
					@include aFont(14, 10, 1);
					@include avPx('line-height', 17, 15, 1);
					font-weight: 400;
					color: #FFF;
					margin-top: 11px;
					transition: .4s ease 0s;
					z-index: 1;

					@media (max-width: 768px) {
						margin-top: 5px;
					}
				}
			}
		}
	}
}
