* {
  outline: transparent;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

button, input, textarea {
  font-family: inherit;
  font-size: inherit;
  border: none;
  background: inherit;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  list-style: none;
}

._imgFit {
  position: relative;
}
._imgFit img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: var(--fitType);
}

html::-webkit-scrollbar, body *::-webkit-scrollbar {
  width: 0.5rem;
  background: hsl(0, 0%, 95%);
}
html::-webkit-scrollbar-thumb, body *::-webkit-scrollbar-thumb {
  width: 0.4rem;
  border: 0.1rem solid hsl(0, 0%, 95%);
  background: hsl(0, 0%, 66%);
}

