.icon-menu {
  display: none;
}

@media (max-width: 992px) {
  .icon-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 48px;
    right: 10px;
    width: 35px;
    height: 30px;
    cursor: pointer;
    border-radius: 4px;
    background: transparent;
    padding: 3px;
    z-index: 11;
    transform: translateY(-50%);
  }
  .icon-menu span {
    position: absolute;
    top: calc(50% - 2px);
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 83%;
    height: 3px;
    border-radius: 8px;
    overflow: hidden;
    background: #2A77E9;
    transition: all 0.3s ease 0s;
  }
  .icon-menu span:first-child {
    top: 5px;
  }
  .icon-menu span:nth-of-type(2) {
    width: 50%;
  }
  .icon-menu span:last-child {
    top: auto;
    bottom: 6px;
  }
  .icon-menu._active span {
    transform: scale(0);
  }
  .icon-menu._active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 2px);
  }
  .icon-menu._active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 992px) and (max-width: 330px) {
  .icon-menu {
    right: 10px;
  }
}
@media (max-width: 992px) {
  .menu nav {
    position: fixed;
