    flex-direction: column;
    justify-content: space-between;
    background: white;
    padding: 50px 10px;
    transform: translateX(100%);
    transition: all 0.5s ease 0s;
    overflow: auto;
    opacity: 0;
    visibility: hidden;
    z-index: 11;
  }
  .menu nav._active {
    transform: translateX(0%);
    opacity: 1;
    visibility: visible;
  }
  .menu nav:before {
    position: fixed;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    background: #6f2232;
    z-index: 2;
  }
}
