.header {
	position: relative;
	border-radius: 0 0 5px 5px;
	box-shadow: 4px 8px 21px 0 rgba(0, 0, 0, 0.15);
	background: #FFF;
	z-index: 2;

	&__content {
		display: flex;
		align-items: center;
		column-gap: 33px;
		padding: 23px 0 25px;

		@media (max-width: 992px) {
			padding: 20px 0;
		}

		@media (max-width: 370px) {
			column-gap: 15px;
		}

		@media(max-width: 1240px) {
		}

		@media (max-width: 1200px) {
		}

		@media (max-width: 1070px) {
		}

		@media (max-width: 992px) {
		}

		@media (max-width: 420px) {
		}
	}

	&__logo {
		display: flex;
		align-items: center;
		justify-content: center;

		a {
			img {
				display: block;
				max-width: 100%;
			}
		}

		@media (max-width: 992px) {
			height: 57px;
		}

	}

	&__body {
		width: 100%;
		display: flex;
		flex-direction: column;

		@media (max-width: 992px) {
			padding-right: 50px;
		}
	}

	&__items {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: 10px;
		margin-bottom: 21px;

		@media (max-width: 992px) {
			margin-bottom: 0;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		column-gap: 14px;

		a:not(a.header__btn.btn) {
			display: flex;
			align-items: center;
			column-gap: 14px;
			font-size: 12px;
			font-weight: 600;
			color: #5B5959;
			transition: color .4s ease 0s;

			address {
				font-style: normal;
			}

			@media (any-hover: hover) {
				&:hover {
					color: #006EDE;
				}
			}

		}

		p {
			font-size: 12px;
			font-weight: 600;
			color: #5B5959;
		}

		&_phone {
			column-gap: 5px;

			a {
				column-gap: 8px;
				font-weight: 700;
			}
		}

		&_mobile {
			@media (max-width: 767px) {
				display: none;
			}
		}
	}

	&__btn {
	}

	&__menu {
		nav {
			ul {
				display: flex;
				align-items: center;
				gap: 26px;

				li {
					position: relative;
					display: flex;
					align-items: center;
					justify-content: center;

					&.arrow {
						a {
							display: flex;
							align-items: center;
							column-gap: 5px;
							z-index: 1;

							._icon {
								display: flex;
								align-items: center;
								justify-content: center;
								transition: transform .4s ease 0s;
							}
						}
					}

					&.arrow {
						position: relative;

						&:before {
							position: absolute;
							content: '';
							top: 0;
							left: 0;
							width: 100%;
							height: 50px;
							z-index: 0;
						}

						@media (any-hover: hover) {
							&:hover {
								ul {
									top: 25px;
									opacity: 1;
									visibility: visible;
								}

								a {
									._icon {
										transform: rotate(180deg);
									}
								}
							}
						}
					}

					a {
						font-size: 12px;
						font-weight: 700;
						text-align: center;
						color: #333;
						transition: color .4s ease 0s;

						@media (any-hover: hover) {
							&:hover {
								color: #006EDE;

								& + svg {
									path {
										fill: #006EDE;
									}
								}
							}
						}

						&.active {
							color: #006EDE;
						}
					}

					&.li-mobile {
						@media (min-width: 992px) {
							display: none;
						}
					}

					ul {
						position: absolute;
						top: 35px;
						left: 0;
						width: 100%;
						border-radius: 5px;
						box-shadow: 4px 8px 21px 0 rgba(0, 0, 0, 0.15);
						background: #FFFFFF;
						opacity: 0;
						visibility: hidden;
						transition: .4s ease 0s;
						z-index: 1;

						li {
							a {
								font-size: 12px;
								font-weight: 700;
								color: #333;
								padding: 10px 20px
							}
						}
					}
				}
			}
		}
	}
}

.menu__footer {
	display: none;
}

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 12px;
	font-weight: 500;
	border: 2px solid #006EDE;
	border-radius: 5px;
	color: white;
	background: #006EDE;
	padding: 8px 17px;
	transition: background .4s ease 0s;
	@media (any-hover: hover) {
		&:hover {
			background: #52a3f5;
		}
	}

}
