.organization {

	&__content {
		padding-bottom: 80px;

		@media (max-width: 768px) {
			padding-bottom: 50px;
		}
	}

	&__header {
		margin-bottom: 21px;
	}

	&__title {
		font-size: 30px;
		line-height: 56px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 56, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__body {
		display: flex;
		gap: 20px;

		@media (max-width: 767px) {
			display: grid;
			grid-template-columns: repeat(2, auto);
		}

		@media (max-width: 380px) {
			display: grid;
			grid-template-columns: auto;
		}
	}

	&__item {
		border-radius: 5px;
		box-shadow: 4px 8px 21px 0 rgba(0, 0, 0, 0.15);
		overflow: hidden;
		transition: transform .4s ease 0s;

		@media (any-hover: hover) {
			&:hover {
				transform: translateY(-5px);
			}
		}


		a {
			border-radius: inherit;

			img {
				display: block;
				max-width: 100%;
				border-radius: inherit;

				@media (max-width: 380px) {
					width: 100%;
				}
			}
		}
	}
}
