.contact {

	&__content {
	}

	&__header {
		margin-bottom: 40px;

		@media (max-width: 768px) {
			margin-bottom: 25px;
		}
	}

	&__title {
		font-size: 30px;
		line-height: 32px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 32, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__body {
		position: relative;
		width: 100%;
		height: 686px;

		@media (any-hover: hover) {
			&:hover {
				.contact__items {
					top: 0;
				}
			}
		}

		@media (max-width: 520px) {
			height: 800px;
		}
	}

	&__map {
		width: 100%;
		height: 100%;

		iframe {
			width: 100%;
			height: 100%;
			border: none;
		}
	}

	&__items {
		position: absolute;
		top: 139px;
		max-width: 1204px;
		display: flex;
		flex-direction: row;
		background: #FFF;
		box-shadow: 0 7.214px 28.056px 0 rgba(0, 0, 0, 0.15);
		transition: top .4s ease 0s;

		@media (max-width: 1365px) {
			top: 10px;
			right: 0;
			left: 0;
			width: fit-content;
			display: grid;
			grid-template-columns: repeat(2, auto);
			justify-content: center;
			margin-inline: auto;
		}

		@media (max-width: 700px) {
			grid-template-columns: auto;
		}

		@media (max-width: 767px) {

		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		row-gap: 5px;
		padding: 18px 35px 25px 35px;

		& > h3 {
			font-size: 17px;
			line-height: 36px;
			@include aFont(17, 15, 1);
			@include avPx('line-height', 36, 20, 1);
			font-weight: 600;
			white-space: nowrap;
			text-transform: uppercase;
			color: #333;
		}

		@media (max-width: 520px) {
			padding: 15px 20px 15px;
		}

		&_line {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				top: 0;
				bottom: 0;
				left: 0;
				width: 1px;
				height: 60%;
				background: #E0E0E0;
				margin-block: auto;
			}

			.contact__item-body {
				a {
					transition: color .4s ease 0s;

					@media (any-hover: hover) {
						&:hover {
							color: #006EDE;
						}
					}
				}
			}
		}

		&_bg {
			background: #006EDE;

			& > h3 {
				color: white;
			}

			.contact__item-body {
				a {
					display: inline-block;
					font-size: 13px;
					line-height: 21px;
					@include aFont(13, 10, 1);
					@include avPx('line-height', 21, 20, 1);
					color: #FFF;

					text-decoration-line: underline;
					text-underline-offset: 3px;
					text-decoration-color: transparent;
					transition: text-decoration-color .4s ease 0s;

					@media (any-hover: hover) {
						&:hover {
							text-decoration-color: white;
						}
					}
				}
			}
		}

		&_img {
			display: flex;
			align-items: center;
			flex-direction: row;
			column-gap: 20px;
			padding: 18px 35px 25px 21px;

			.contact__item-body {
				strong {
					display: inline-block;
					font-size: 12px;
					line-height: 29px;
					@include aFont(12, 10, 1);
					@include avPx('line-height', 29, 20, 1);
					font-weight: 600;
					white-space: nowrap;
					color: #333;
					margin-bottom: 4px;
				}

				a {
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 12px;
					line-height: 24px;
					@include aFont(12, 10, 1);
					@include avPx('line-height', 24, 20, 1);
					font-weight: 600;
					color: #FFF;
				}
			}
		}
	}

	&__item-body {
		display: flex;
		flex-direction: column;

		address {
			font-size: 13px;
			line-height: 21px;
			@include aFont(13, 10, 1);
			@include avPx('line-height', 21, 20, 1);
			font-style: normal;
			color: #7D7D7D;
		}

		a {
			display: inline-block;
			font-size: 13px;
			line-height: 21px;
			@include aFont(13, 10, 1);
			@include avPx('line-height', 21, 20, 1);
			color: #7D7D7D;
		}
	}

	&__item-img {
		min-width: 72px;
		height: 72px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		overflow: hidden;
	}
}
