.safety-intro {
	background: linear-gradient(90deg, rgba(32, 32, 32, 0.50) 0.94%, rgba(32, 32, 32, 0.00) 66.35%),
	url(../images/safety/bg.png) no-repeat center/cover;

	.intro__content {
		padding: 268px 0 219px 0;

		@media (max-width: 768px) {
			padding: 50px 0;
		}

		& > h2 {
			font-size: 55px;
			line-height: 60px;
			@include aFont(55, 22, 1);
			@include avPx('line-height', 60, 20, 1);
			font-weight: 700;
			text-transform: uppercase;
			color: #FFF;
			margin-bottom: 21px;
		}

		& > p {
			font-size: 20px;
			line-height: 29px;
			@include aFont(20, 15, 1);
			@include avPx('line-height', 29, 20, 1);
			color: #FFF;
			margin-bottom: 30px;

			@media (max-width: 768px) {
				margin-bottom: 15px;
			}

			@media (max-width: 520px) {
				br {
					display: none;
				}
			}
		}

		& > a {
			width: max-content;
			font-size: 18px;
			@include aFont(18, 16, 1);
			padding: 20px 65px;
		}
	}
}

