.icon-menu {
	display: none;
}

@media(max-width: 992px) {
	.icon-menu {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 48px;
		right: 10px;
		width: 35px;
		height: 30px;
		cursor: pointer;
		border-radius: 4px;
		background: transparent;
		padding: 3px;
		z-index: 11;
		transform: translateY(-50%);

		span {
			position: absolute;
			top: calc(50% - 2px);
			left: 0;
			right: 0;
			margin: 0 auto;
			width: 83%;
			height: 3px;
			border-radius: 8px;
			overflow: hidden;
			background: #2A77E9;
			transition: all 0.3s ease 0s;

			&:first-child {
				top: 5px;
			}

			&:nth-of-type(2) {
				width: 50%;
			}

			&:last-child {
				top: auto;
				bottom: 6px;
			}
		}

		&._active {
			span {
				transform: scale(0);

				&:first-child {
					transform: rotate(-45deg);
					top: calc(50% - 2px);
				}

				&:last-child {
					transform: rotate(45deg);
					bottom: calc(50% - 1px);
				}
			}
		}

		@media (max-width: 330px) {
			right: 10px;
		}
	}

	.menu {
		nav {
			position: fixed;
			top: 123px;
			right: 0;
			width: 30%;
			//height:          100%;
			//height:          100vh;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			background: white;
			padding: 50px 10px;
			transform: translateX(100%);
			transition: all .5s ease 0s;
			overflow: auto;
			opacity: 0;
			visibility: hidden;
			z-index: 11;

			@media (max-width: 736px) {

			}

			&._active {
				transform: translateX(0%);
				opacity: 1;
				visibility: visible;
			}

			&:before {
				position: fixed;
				content: '';
				top: 0;
				left: 0;
				width: 100%;
				background: #6f2232;
				z-index: 2;
			}

			@media (max-width: 992px) {
				top: 107px;
				width: 50%;
			}

			@media (max-width: 767px) {
				top: 97px;
			}

			@media (max-width: 420px) {

				width: 80%;
			}

			@media (max-width: 380px) {

				width: 100%;
			}

			@media (max-width: 379px) {

				width: 100%;
			}
		}

		ul {
			width: 100%;
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 50px;

			@media (min-width: 768px) {
				flex-wrap: wrap;
			}

			&.menu-three {
				.menu__link {
					display: flex;
					align-items: center;
					column-gap: 10px;
					font-family: 'Roboto Flex', sans-serif;
				}
			}
		}

		li {
			&:not(:last-child) {

			}

			&_mobile {
				display: block;
			}
		}

		&__footer {
			display: flex;
			flex-direction: column;
			align-items: center;
			row-gap: 10px;
		}

		&__item {
			a {
				text-align: center;
			}
		}
	}
}
