.popup-request {
	-webkit-overflow-scrolling: touch;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 157px 10px 30px;
	transition: visibility 0.8s ease 0s;
	visibility: hidden;
	z-index: 1 !important;

	&::before {
		position: fixed;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.9);
		transition: opacity 0.8s ease 0s;
		opacity: 0;
	}

	&.show {
		visibility: visible;
		overflow: hidden !important;

		&::before {
			opacity: 1;
		}

		.popup-request__body {
			transform: scale(1);
		}
	}

	&._active {
		overflow: hidden !important;
		visibility: visible;

		@media (max-width: 767px) {
			overflow: auto !important;
		}

		&::before {
			opacity: 1;
		}

		.popup-request__body {
			transition: all .3s ease .2s;

			@media (max-width: 1920px) {
				transform: scale(1) translate(60%, -24%);
			}

			@media (max-width: 1650px) {
				transform: scale(1) translate(50%, -16%);
			}

			@media (max-width: 1366px) {
				transform: scale(1) translate(49%, -14%);
			}

			@media (max-width: 1024px) {
				transform: scale(1) translate(49%, -14%);
			}

			@media (max-width: 992px) {
				transform: scale(1) translate(36%, -18%);
			}

			@media (max-width: 767px) {
				transform: scale(1);
			}
		}
	}

	&__content {
		width: 100%;
		min-height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		flex: 1 1 auto;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__body {
		width: 1089px;
		height: 1089px;
		display: flex;
		flex-direction: column;
		border-radius: 50%;
		overflow: hidden;
		background: #006EDE;
		transition: .3s ease 0s;
		padding: 0 !important;
		transform: scale(0);

		@media (max-width: 992px) {
			padding: 45px 20px 35px 20px;
		}

		@media (max-width: 767px) {
			max-width: 400px;
			width: 100%;
			height: auto;
			border-radius: 18px;

		}
	}

	&__close {
		position: absolute;
		width: 25px;
		height: 25px;
		cursor: pointer;
		z-index: 30;

		@media (max-width: 1920px) {
			top: 315px;
			//right: 328px;
			//@include adaptive(top, 1650px, 1920px, 170px, 315px);
			@include adaptive(right, 1650px, 920px, 350px, 328px);
		}

		@media (max-width: 1650px) {
			top: 170px;
			right: 300px;

			@include adaptive(right, 1024px, 1650px, 600px, 328px);
		}

		@media (max-width: 767px) {
			top: 10px;
			right: 10px;
		}

		&:before,
		&:after {
			position: absolute;
			content: '';
			inset: 0;
			width: 20px;
			height: 2px;
			background: white;
			margin-inline: auto;
			margin-block: auto;
		}

		&:before {
			rotate: 45deg;
		}

		&:after {
			rotate: -45deg;
		}
	}
}

.popup-request {

	&__content {
	}

	&__body {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding-left: 174px !important;

		@media (max-width: 1024px) {
			padding-left: 140px !important;
		}

		@media (max-width: 767px) {
			padding: 40px 10px !important;
		}
	}

	&__close {

	}

	&__inner {
		max-width: 355px;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;

		@media (max-width: 767px) {
			max-width: 100%;
		}
	}

	&__title {
		font-size: 27px;
		font-style: normal;
		font-weight: 800;
		line-height: 27px;
		text-transform: uppercase;
		color: #FFF;
		margin-bottom: 10px;
	}

	&__subtitle {
		font-size: 13px;
		line-height: 14px;
		font-style: normal;
		font-weight: 400;
		text-align: center;
		color: #FFF;
		margin-bottom: 26px;
	}

	&__policy {
		font-size: 13px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		text-align: center;
		color: #FFF;
	}
}

.popup {

	&_popup {
	}
}

.request-form {
	position: relative;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 16px;
	margin-bottom: 27px;
	z-index: 1;

	label {
		position: relative;
		width: 100%;


		div.error {
			position: absolute;
			left: 22px;
			bottom: -7px;
			font-size: 10px;
			color: #dc3545;
			background: #FFFFFF;
			padding: 0 5px;
		}
	}

	input {
		width: 100%;
		border: 1px solid transparent;
		border-radius: 55px;
		background: #FFF;
		padding: 19px 32px;

		@media (max-width: 768px) {
			border-radius: 12px;
		}

		&::placeholder {
			font-size: 13px;
			font-style: normal;
			font-weight: 400;
			line-height: normal;
			color: #2C2B33;
		}

		&:focus {
			color: #212529;
			border-color: #86b7fe;
			outline: 0;
			box-shadow: 0 0 0 0.25rem rgb(13 110 253 / 25%);
		}

		&.error {
			border-color: #dc3545;
			padding-right: calc(1.5em + 0.75rem);
			background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12' width='12' height='12' fill='none' stroke='%23dc3545'%3e%3ccircle cx='6' cy='6' r='4.5'/%3e%3cpath stroke-linejoin='round' d='M5.8 3.6h.4L6 6.5z'/%3e%3ccircle cx='6' cy='8.2' r='.6' fill='%23dc3545' stroke='none'/%3e%3c/svg%3e");
			background-repeat: no-repeat;
			background-position: right calc(0.375em + 0.1875rem) center;
			background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
		}

		&:focus.error {
			border-color: #dc3545;
			box-shadow: 0 0 0 0.25rem rgb(220 53 69 / 25%);
		}

		&.success {
			border-color: #198754;
			padding-right: calc(1.5em + 0.75rem);
			background-image: url("data:image/svg+xml,%3csvg xmlns= 'http://www.w3.org/2000/svg' viewBox= '0 0 8 8' %3e%3cpath fill= '%23198754' d= 'M2.3 6.73L.6 4.53c-.4-1.04.46-1.4 1.1-.8l1.1 1.4 3.4-3.8c.6-.63 1.6-.27 1.2.7l-4 4.6c-.43.5-.8.4-1.1.1z' /%3e%3c/svg%3e");
			background-repeat: no-repeat;
			background-position: right calc(0.375em + 0.1875rem) center;
			background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
		}

		&:focus.success {
			border-color: #198754;
			box-shadow: 0 0 0 0.25rem rgb(25 135 84 / 25%);
		}
	}

	button {
		width: 100%;
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		border-radius: 55px;
		color: #FFF;
		background: #2C2B33;
		padding: 15px;

		@media (max-width: 768px) {
			border-radius: 12px;
		}
	}
}
