.news {

	&__content {
		padding-top: 14px;
	}

	&__header {
		margin-bottom: 38px;

		@media (max-width: 768px) {
			margin-bottom: 25px;
		}

		h2 {
			font-size: 30px;
			line-height: 32px;
			@include aFont(30, 20, 1);
			@include avPx('line-height', 32, 20, 1);
			font-weight: 700;
			color: #000;
		}
	}

	&__article {
		border-bottom: 1px solid #006EDE;

		&:not(:nth-of-type(1)) {
			padding-top: 50px;

			@media (max-width: 768px) {
				padding-top: 25px;
			}
		}

		//&:nth-child(3n) {
		//	border-color: transparent;
		//}

		&_border {
			border-color: transparent;
		}

		& > header {
			margin-bottom: 40px;

			@media (max-width: 768px) {
				margin-bottom: 25px;
			}

			& > h2 {
				font-size: 28px;
				@include aFont(28, 18, 1);
				font-weight: normal;
				color: #2C2B33;
				margin-bottom: 16px;
			}

			& > time {
				color: #7D7D7D;
				font-size: 15px;
				@include aFont(15, 12, 1);
				font-weight: 400;
			}
		}

	}

	&__body {
		padding-bottom: 50px;

		@media (max-width: 768px) {
			padding-bottom: 25px;
		}

		& > h3 {
			font-size: 25px;
			@include aFont(25, 18, 1);
			font-weight: 800;
			color: #333;
			margin-bottom: 25px;
		}

		& > p {
			font-size: 16px;
			line-height: 31px;
			@include aFont(16, 14, 1);
			@include avPx('line-height', 31, 20, 1);
			font-weight: 400;
			color: #333437;

			&:not(:last-child) {
				margin-bottom: 45px;

				@media (max-width: 768px) {
					margin-bottom: 25px;
				}
			}
		}
	}

	&__picture {
		border-radius: 10px;
		margin-bottom: 35px;

		img {
			display: block;
			max-width: 100%;
			width: 100%;
			border-radius: inherit;
		}
	}
}

.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 5px;
	margin-bottom: 58px;

	@media (max-width: 768px) {
		margin-bottom: 25px;
	}
}

.pagination button {
	width: 45px;
	height: 45px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 25px;
	@include aFont(25, 18, 1);
	color: #363737;
	background: #DFDFDF;
	transition: background .4s ease 0s;

	@media (any-hover: hover) {
		&:hover {
			background: rgba(#006EDE, .33);
		}
	}

	@media (max-width: 768px) {
		width: 35px;
		height: 35px;
	}
}

.hidden {
	clip: rect(0 0 0 0);
	clip-path: inset(50%);
	height: 1px;
	overflow: hidden;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}

.pagination button.active {
	background: #006EDE;
	color: white;
}
