.concepts {

	&__content {
		padding-bottom: 59px;

		@media (max-width: 768px) {
			padding-bottom: 50px;
		}
	}

	&__header {
		margin-bottom: 15px;
	}

	&__title {
		font-size: 30px;
		line-height: 56px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 56, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__body {
		ul {
			max-width: 1082px;

			li {
				p {
					font-size: 17px;
					line-height: 35px;
					@include aFont(17, 12, 1);
					@include avPx('line-height', 35, 20, 1);
					color: #000;
				}
			}
		}
	}
}
