.contacts {

	&__content {
		padding-top: 14px;
		padding-bottom: 128px;
	}

	&__header {
		margin-bottom: 38px;

		@media (max-width: 768px) {
			margin-bottom: 25px;
		}
	}

	&__title {
		font-size: 30px;
		line-height: 32px;
		@include aFont(30, 20, 1);
		@include avPx('line-height', 32, 20, 1);
		font-weight: 700;
		color: #000;
	}

	&__top {
		display: flex;
		align-items: center;
		column-gap: 116px;
		margin-bottom: 30px;

		@media (max-width: 992px) {
			column-gap: 50px;
		}

		@media (max-width: 768px) {
			flex-direction: column;
			align-items: flex-start;
			row-gap: 15px;
		}
	}

	&__top-left {
		position: relative;

		&:before {
			position: absolute;
			content: '';
			top: 0;
			bottom: 0;
			right: -58px;
			width: 1px;
			height: 90%;
			background: #000;
			margin-block: auto;
			z-index: 0;

			@media (max-width: 992px) {
				right: -26px;
			}

			@media (max-width: 767px) {
				content: none;
			}
		}


		& > h3 {
			font-size: 40px;
			line-height: 55px;
			@include aFont(40, 20, 1);
			@include avPx('line-height', 55, 20, 1);
			font-weight: 400;
			color: #000;

			span {
				color: #006EDE;
			}

			@media (max-width: 992px) {
				br {
					display: none;
				}
			}
		}
	}

	&__top-right {

		& > p {
			font-size: 20px;
			line-height: 27px;
			@include aFont(20, 15, 1);
			@include avPx('line-height', 27, 20, 1);
			font-weight: 400;
			color: #000;

			@media (max-width: 992px) {
				br {
					display: none;
				}
			}
		}
	}

	&__body {
		display: flex;
		align-items: center;
		column-gap: 80px;
		margin-bottom: 36px;

		@media (max-width: 992px) {
			column-gap: 20px;
		}

		@media (max-width: 767px) {
			flex-direction: column;
			align-items: flex-start;
			row-gap: 20px;
		}
	}

	&__left {
		flex: 1;

		@media (max-width: 767px) {
			width: 100%;
			flex: unset;
		}
	}

	&__left-map {
		position: relative;
		max-width: 662px;
		width: 100%;
		height: 588px;
		border-radius: 15px;

		iframe {
			width: 100%;
			height: 100%;
			border-radius: inherit;
			border: none;
		}

		@media (max-width: 767px) {
			max-width: 100%;
			height: 250px;
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		row-gap: 47px;

		@media (max-width: 767px) {
			flex-direction: row;
			column-gap: 10px;
		}

		@media (max-width: 520px) {
			flex-direction: column;
		}
	}

	&__right-item {
		display: flex;
		flex-direction: column;

		small {
			display: inline-block;
			font-size: 21px;
			line-height: 24px;
			@include aFont(21, 15, 1);
			@include avPx('line-height', 24, 20, 1);
			font-style: normal;
			font-weight: 400;
			color: #939393;
			margin-bottom: 26px;
		}

		address {
			font-size: 36px;
			line-height: 42px;
			@include aFont(36, 20, 1);
			@include avPx('line-height', 42, 20, 1);
			font-style: normal;
			font-weight: 500;
			color: #232323;
		}

		.btn {
			width: max-content;
			padding: 13px 29px;
			margin-top: 33px;
		}
	}

	&__right-city {
		display: flex;
		align-items: center;
		column-gap: 13px;
		margin-bottom: 26px;
	}

	&__right-icon {
	}

	&__inner {
		display: flex;
		align-items: flex-end;
		column-gap: 51px;
		border-radius: 10px;
		background: url(../images/contact/bg.png) no-repeat center/cover rgba(#1E1E1E, .5);
		background-blend-mode: color;
		padding: 35px 89px 38px 85px;

		@media (max-width: 767px) {
			flex-direction: column;
			row-gap: 20px;
			padding: 20px;
		}
	}

	&__inner-left {
		& > h2 {
			font-size: 54px;
			line-height: 61px;
			@include aFont(54, 20, 1);
			@include avPx('line-height', 61, 20, 1);
			font-style: normal;
			font-weight: 700;
			color: #FFF;
			margin-bottom: 18px;
		}

		& > p {
			font-size: 24px;
			line-height: 59px;
			@include aFont(24, 15, 1);
			@include avPx('line-height', 59, 20, 1);
			font-style: normal;
			font-weight: 400;
			color: #FFF;
		}
	}

	&__inner-right {
	}
}

.tel {
	color: #006EDE;
	font-size: 36px;
	line-height: 42px;
	@include aFont(36, 20, 1);
	@include avPx('line-height', 42, 20, 1);
	font-style: normal;
	font-weight: 400;
	margin-bottom: 31px;
	text-decoration-line: underline;
	text-underline-offset: 3px;
	text-decoration-thickness: 1px;
	text-decoration-color: transparent;
	transition: .5s ease 0s;

	@media (any-hover: hover) {
		&:hover {
			text-decoration-color: inherit;
		}
	}
}

.mailto {
	font-size: 34px;
	line-height: 40px;
	@include aFont(34, 20, 1);
	@include avPx('line-height', 40, 20, 1);
	font-style: normal;
	font-weight: 400;
	color: #000;
	text-decoration-line: underline;
	text-underline-offset: 6px;
	text-decoration-thickness: 1px;
	text-decoration-color: transparent;
	transition: .5s ease 0s;

	@media (any-hover: hover) {
		&:hover {
			text-decoration-color: inherit;
		}
	}
}

.btn {
}

.form {
	position: relative;
	width: 451px;
	height: 479px;
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid transparent;
	border-radius: 10px;
	background: #FFF;
	padding: 26px 38px;

	@media (max-width: 767px) {
		padding: 20px;
	}

	@media (max-width: 600px) {
		width: 100%;
	}

	//&:has(input.error) {
	//	border-color: #dc3545;
	//	box-shadow: 0 0 0 0.25rem rgb(220 53 69 / 25%);
	//}
	//
	//&:has(input.success) {
	//	border-color: #198754;
	//	box-shadow: 0 0 0 0.25rem rgb(25 135 84 / 25%);
	//}

	& > p {
		font-size: 15px;
		line-height: 22px;
		@include aFont(15, 12, 1);
		@include avPx('line-height', 22, 20, 1);
		font-style: normal;
		font-weight: 500;
		color: #232323;

		a {
			color: #006EDE;
			text-decoration-line: underline;
			text-underline-offset: 3px;
			text-decoration-thickness: 1px;
			text-decoration-color: transparent;
			transition: .5s ease 0s;

			@media (any-hover: hover) {
				&:hover {
					text-decoration-color: inherit;
				}
			}
		}
	}

	& > button {
		position: absolute;
		left: 0;
		right: 0;
		bottom: -142px;
		width: 198px;
		height: 198px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		row-gap: 12px;
		border-radius: 50%;
		background: #006EDE;
		margin-inline: auto;
		transition: .4s ease 0s;

		@media (any-hover: hover) {
			&:hover {
				box-shadow: 0 0 10px #006EDE;
			}
		}

		span {
			font-size: 23px;
			line-height: 25px;
			@include aFont(23, 15, 1);
			@include avPx('line-height', 25, 20, 1);
			font-style: normal;
			font-weight: 500;
			color: #FFF;
		}

		@media (max-width: 767px) {
			bottom: -80px;
			width: 150px;
			height: 150px;
		}
	}

	&__item {
		position: relative;
		width: 100%;

		div.error {
			position: absolute;
			left: 22px;
			bottom: -7px;
			font-size: 12px;
			color: #dc3545;
			background: #FFFFFF;
			padding: 0 5px;
		}

		&:not(:last-child) {
			margin-bottom: 28px;
		}

		& > input {
			width: 100%;
			border-bottom: 1px solid #232323;
			padding: 29px 0;
		}

		& > input:focus ~ label {
			top: -36px;

			@media (max-width: 767px) {
				top: -25px;
			}

			@media (max-width: 767px) {
				top: -15px;
			}
		}

		& > input:focus + label {
			top: -36px;

			@media (max-width: 767px) {
				top: -25px;
			}
			@media (max-width: 767px) {
				top: -15px;
			}
		}

		& > input:valid ~ label {
			top: -36px;

			@media (max-width: 767px) {
				top: -25px;
			}
			@media (max-width: 767px) {
				top: -15px;
			}
		}

		& > input:valid + label {
			top: -36px;

			@media (max-width: 767px) {
				top: -25px;
			}
			@media (max-width: 767px) {
				top: -15px;
			}
		}

		//& > input:invalid ~ label {
		//	top: -36px;
		//}

		//& > input:invalid + label {
		//	top: -36px;
		//}

		& > label {
			position: absolute;
			left: 0;
			top: 14px;

			font-size: 22px;
			line-height: 48px;
			@include aFont(22, 15, 1);
			@include avPx('line-height', 48, 20, 1);
			font-style: normal;
			font-weight: 500;
			color: #232323;
			transition: all .4s ease 0s;
		}
	}
}


.popup {
	-webkit-overflow-scrolling: touch;
	z-index: 100;
	padding: 30px 10px;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-transition: visibility 0.8s ease 0s;
	transition: visibility 0.8s ease 0s;
	visibility: hidden;
}

.popup::before {
	content: "";
	background-color: rgba(0, 0, 0, 0.5);
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
	-webkit-transition: opacity 0.8s ease 0s;
	transition: opacity 0.8s ease 0s;
}

.popup._active {
	overflow: auto;
	visibility: visible;
}

.popup._active::before {
	opacity: 1;
}

.popup._active .popup__body {
	-webkit-transition: all 0.3s ease 0.2s;
	transition: all 0.3s ease 0.2s;
	-webkit-transform: scale(1);
	transform: scale(1);
}

.popup__content {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	min-height: 100%;
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
	width: 100%;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	align-items: center;
}

.popup__body {
	width: 100%;
	max-width: 583px;
	border-radius: 6px;
	-webkit-box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2),
	0 7px 20px 0 rgba(0, 0, 0, 0.17);
	box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
	-webkit-transform: scale(0);
	transform: scale(0);
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	background-color: #fff;
	padding: 50px;
}

.popup__close {
	width: 15px;
	height: 15px;
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	z-index: 30;
	background: url("../images/cross.svg") no-repeat center/contain;
}

.popup__title {
	font-size: 30px;
	line-height: 35px;
	font-weight: 700;
	letter-spacing: 0.005em;
	text-transform: uppercase;
	text-align: center;
	margin: 0 0 50px;
}

.popup__text {
	font-size: 18px;
	line-height: normal;
	font-weight: 700;
	text-transform: uppercase;
}
