@use 'sass:math';
@import 'vars';
@import 'reset';
@import 'utils';
@import 'mixins';
@import 'fonts';

html {
	scroll-behavior: smooth;
	//scroll-padding: var(--scroll-padding, 125px);
	background: #FFF;

	&.dark {
		background: #232323;

		header {
			background: #232323;
		}
	}
}

body {
	position: relative;
	font-family: 'Montserrat', sans-serif;
	font-size: 16px;
	font-weight: 400;

	&._lock {
		overflow: hidden;
	}

	&.lock {
		overflow: hidden;
	}
}

.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;

	&.programs,
	&.safety,
	&.schedule,
	&.educational {
		.bread-crumbs {
			.bread-crumbs__content {
				ul {
					li {
						span {
							color: #006EDE;
						}
					}
				}
			}
		}
	}
}

.page {
	position: relative;
	flex: 1 1 auto;
}

[class*='__container'] {
	--max-width: 1200px;
	--padding: 20px;
	width: min(100% - var(--padding), var(--max-width));
	margin-inline: auto;
}

[class*='__container'][data-type='rental'] {
	--max-width: 1836px;
}

@import '../pug/static/header/header';
@import '../pug/static/header/burger/burger';
@import '../pug/static/footer/footer';

@import '../pug/components/index/intro/intro';
@import '../pug/components/index/our-services/our-services';
@import '../pug/components/index/company/company';
@import '../pug/components/index/information/information';
@import '../pug/components/index/contact/contact';
@import '../pug/includes/bread-crumbs/bread-crumbs';
@import '../pug/components/programs/programs';

@import '../pug/components/safety/safety-intro/safety-intro';
@import '../pug/components/safety/description/description';
@import '../pug/components/safety/concepts/concepts';
@import '../pug/components/safety/organization/organization';
@import '../pug/components/schedule/schedule';
@import '../pug/components/educational/educational';
@import '../pug/components/news/news';
@import '../pug/components/contact/contact';
@import '../pug/includes/popup/popup';

.swiper-pagination {
	z-index: 1 !important;
}
